<template lang="pug">
    div(class="page")
        Header
        section(class='build-project')
            div(class='container')
                div(class='title')
                    h2 {{ $t("Build") }} 
                        span {{ $t("your-project") }} 
                div(class='build-content')
                    div(class='alert bg-danger alert-warning alert-dismissible fade show' role='alert' v-if="notSent")
                        p {{ $t("error-message") }}
                        button(type='button' class='close' data-dismiss='alert' aria-label='Close')
                            span(aria-hidden='true') &times;
                    div(class='form-area')
                        form(ref="buildForm" enctype="multipart/form-data" action='')
                            .row
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('full-name')" class='form-control' type='text' name="name" v-model="buildFormData.name")
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('phone-number')" class='form-control' type='tel' name="phone" v-model="buildFormData.phone")
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('company-name')" class='form-control' type='text' name="company" v-model="buildFormData.company")
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder="$t('email')" class='form-control' type='email' name="email" v-model="buildFormData.email")
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        div(class='custom-select-wrapper')
                                            vue-picker(v-model='buildFormData.project_type' class='form-control' :placeholder="$t('project-type')")
                                                vue-picker-option(value='Mobile') Mobile Application
                                                vue-picker-option(value='Web') Web Application
                                                vue-picker-option(value='System' text='System') System
                                div(class="col-lg-6")
                                    div(class='form-group')
                                        input(:placeholder='Country' class='form-control' type='text' name="country" v-model="buildFormData.country")
                                div(class="col-lg-6")
                                    div(class='custom-range time-range')
                                        .layout-slider
                                            h4.subtitle {{ $t("timeline") }}
                                            vue-range-slider(ref="slider" v-model="buildFormData.timeline" :min="minTime" :max="maxTime" :formatter="formatterTime" :tooltip-merge="tooltipMerge" :enable-cross="enableCross")
                                div(class="col-lg-6")
                                    div(class='custom-range budget-range')
                                        .layout-slider
                                            h4.subtitle {{ $t("budget") }}
                                            vue-range-slider(ref="slider" v-model="buildFormData.budget" :min="minBudget" :max="maxBudget" :formatter="formatterBudget" :tooltip-merge="tooltipMerge" :enable-cross="enableCross")
                                div(class="col-lg-12")
                                    div(class='upload')
                                        fa(class="theicon" :icon="['fas', 'upload']")
                                        label.sr-only(for='file') Upload file
                                        input(type='file' id="file" ref="file" name="file" v-on:change="handleFileUpload")
                                div(class="col-lg-12")
                                    div(class='form-group button')
                                        button(class='btn hvr-sweep-to-bottom' type='submit' v-on:click="submitFile") {{ $t("send") }} 
                                div(class="col-lg-12" v-if="sent")
                                    p.successfully {{ successMessage }}
        Footer
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import { VuePicker, VuePickerOption } from "@invisiburu/vue-picker";
import "@invisiburu/vue-picker/dist/vue-picker.min.css";
import "vue-range-component/dist/vue-range-slider.css";
import VueRangeSlider from "vue-range-component";

// @ is an alias to /src
import Header from "@/components/global/Header.vue";
import Footer from "@/components/global/Footer.vue";

Vue.use(VueAxios, axios);

export default {
    name: "BuildProject",
    components: {
        Header,
        Footer,
        VuePicker,
        VuePickerOption,
        VueRangeSlider,
    },
    data: () => ({
        selected: null,
        options: ["list", "of", "options"],

        successMessage: "",
        sent: false,
        notSent: false,
        buildFormData: {
            name: "",
            phone: "",
            company: "",
            timeline: [0, 4],
            budget: [0, 2000],
            email: "",
            project_type: "",
            country: "",
            file: "",
        },
    }),
    created() {
        this.minTime = 0;
        this.maxTime = 36;
        this.minBudget = 0;
        this.maxBudget = 20000;
        this.enableCross = true;
        this.tooltipMerge = false;
        this.formatterTime = (value) => `${value} mon`;
        this.formatterBudget = (value) => `${value} $`;
        VueRangeSlider.methods.handleKeyup = () => console.log;
        VueRangeSlider.methods.handleKeydown = () => console.log;
    },
    methods: {
        submitFile(e) {
            e.preventDefault();
            let formData = new FormData();
            formData.append("name", this.buildFormData.name);
            formData.append("phone", this.buildFormData.phone);
            formData.append("company", this.buildFormData.company);
            formData.append("timeline", this.buildFormData.timeline);
            formData.append("budget", this.buildFormData.budget);
            formData.append("email", this.buildFormData.email);
            formData.append("project_type", this.buildFormData.project_type);
            formData.append("country", this.buildFormData.country);
            formData.append("file", this.buildFormData.file);
            axios
                .post("build-project", formData, {
                    headers: {
                        "Content-Type": "multipart/form-data",
                    },
                })
                .then((result) => {
                    this.successMessage = result.data.data.message;
                    this.sent = true;
                    this.notSent = false;
                    this.buildFormData.name = "";
                    this.buildFormData.phone = "";
                    this.buildFormData.company = "";
                    this.buildFormData.email = "";
                    this.buildFormData.project_type = "";
                    this.buildFormData.country = "";
                    this.buildFormData.file = "";
                    // console.warn(result);
                })
                .catch((error) => {
                    this.notSent = true;
                    this.sent = false;
                    console.log(error);
                });
            this.$refs.buildForm.reset();
        },
        handleFileUpload() {
            this.buildFormData.file = this.$refs.file.files[0];
        },
    },
};
</script>

<style lang="scss">
@import "../sass/global";
.vue-picker__opener {
    padding: 0;
    border: none !important;
    color: #848484;
    &:focus {
        outline: none;
        border: none;
        box-shadow: none;
        .vue-picker__opener-ico {
            @include transform(rotate(180deg));
            @include transition(all ease 0.4s);
        }
    }
}
.vue-picker__dropdown {
    background: var(--col-dd-bg);
    border: 1px solid #e3e3e3;
    top: 105%;
    border-radius: 10px;
    box-shadow: 1px 1px 15px -10px #858585;
}
.vue-range-slider.slider-component {
    z-index: 0;
}
</style>
